import React from "react";
import s from "./Post.module.css";

type PostType = {
    message: string;
    likesCount: number
}

function Post (props: PostType) {
    return (
            <div  className={s.item} >
                <img src=' data:image/jpeg;base64,' />
                {props.message}
                <div>
                <span>like</span> {props.likesCount}
                </div>
            </div>
    )
}
export default Post;